* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Helvetica',sans-serif;
  background-color: #112;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    90deg, rgba(2,0,36,1) 0%, rgba(36,99,182,1) 0%, rgba(120,203,233,1) 100%
  );
  color: #e2e2e2;
}

.coin-input::placeholder{
  color: #e2e2e2;
}
